import React,{Component} from 'react';

import './Box1.css';




class Box1 extends Component {
    constructor(props){
        super(props)
        this.state = {
          selectedOption1:"",
          q1 : false,
          q2 :false,
          selectedOption2:"",
          arr1 : [0,0],
          sum1:0,
          part1b:false,
          part1e:false,
          ans1:false,


          selectedOption3:"",
          q3 : false,
          q4 :false,
          selectedOption4:"",
          arr2 : [0,0],
          sum2:0,
          part2b:false,
          part2e:false,
          ans2:false,


          selectedOption5:"",
          q5 : false,
          q6 :false,
          selectedOption6:"",
          arr3 : [0,0],
          sum3:0,
          part3b:false,
          part3e:false,
          ans3:false,



        };
        this.handleOptionChange1 = this.handleOptionChange1.bind(this)
        this.handleOptionChange2 = this.handleOptionChange2.bind(this)
        this.handlepart1 = this.handlepart1.bind(this)
        this.handleOptionChange3 = this.handleOptionChange3.bind(this)
        this.handleOptionChange4 = this.handleOptionChange4.bind(this)
        this.handlepart2 = this.handlepart2.bind(this)
        this.handleOptionChange5 = this.handleOptionChange5.bind(this)
        this.handleOptionChange6 = this.handleOptionChange6.bind(this)
        this.handlepart3 = this.handlepart3.bind(this)
    }
    handleOptionChange1(event){
            this.setState({selectedOption1:event.target.value,q1:true})
            var x = event.target.value;
            let y = Number(x);
      
            let new_arr = this.state.arr1.slice();
            new_arr[0] = y;
            this.setState({arr1:new_arr})
           
          //  {console.log(this.state.arr1[0] + " "+this.state.arr1[1]+" "+this.state.part1b+" "+this.state.part1e)}
              //  {console.log("hello")}
    }
    handleOptionChange2(event){
        this.setState({selectedOption2:event.target.value,q2:true})
        var x = event.target.value;
        let y = Number(x);
  
        let new_arr = this.state.arr1.slice();
        new_arr[1] = y;
        this.setState({arr1:new_arr})
      
     
      //  {console.log(this.state.arr1[0] + " "+this.state.arr1[1]+" "+this.state.part1b+" "+this.state.part1e)}
}
handlepart1(event){
    this.setState({ans1:true})
    if(this.state.q1 && this.state.q2){
        let s = this.state.arr1[0] + this.state.arr1[1]
        if(s>3){
            this.setState({part1b:true,part1e:false})
        }else{
            this.setState({part1b:false,part1e:true})
        }
    }
}


        handleOptionChange3(event){
            this.setState({selectedOption3:event.target.value,q3:true})
            var x = event.target.value;
            let y = Number(x);

            let new_arr = this.state.arr2.slice();
            new_arr[0] = y;
            this.setState({arr2:new_arr})
        
        //  {console.log(this.state.arr1[0] + " "+this.state.arr1[1]+" "+this.state.part1b+" "+this.state.part1e)}
            //  {console.log("hello")}
        }
        handleOptionChange4(event){
        this.setState({selectedOption4:event.target.value,q4:true})
        var x = event.target.value;
        let y = Number(x);

        let new_arr = this.state.arr2.slice();
        new_arr[1] = y;
        this.setState({arr2:new_arr})


        //  {console.log(this.state.arr1[0] + " "+this.state.arr1[1]+" "+this.state.part1b+" "+this.state.part1e)}
        }
        handlepart2(event){
        this.setState({ans2:true})
        if(this.state.q3 && this.state.q4){
        let s = this.state.arr2[0] + this.state.arr2[1]
        if(s>3){
            this.setState({part2b:true,part2e:false})
        }else{
            this.setState({part2b:false,part2e:true})
        }
        }
        }




        handleOptionChange5(event){
            this.setState({selectedOption5:event.target.value,q5:true})
            var x = event.target.value;
            let y = Number(x);

            let new_arr = this.state.arr3.slice();
            new_arr[0] = y;
            this.setState({arr3:new_arr})
        
        //  {console.log(this.state.arr1[0] + " "+this.state.arr1[1]+" "+this.state.part1b+" "+this.state.part1e)}
            //  {console.log("hello")}
        }
        handleOptionChange6(event){
        this.setState({selectedOption6:event.target.value,q6:true})
        var x = event.target.value;
        let y = Number(x);

        let new_arr = this.state.arr3.slice();
        new_arr[1] = y;
        this.setState({arr3:new_arr})


        //  {console.log(this.state.arr1[0] + " "+this.state.arr1[1]+" "+this.state.part1b+" "+this.state.part1e)}
        }
        handlepart3(event){
        this.setState({ans3:true})
        if(this.state.q5 && this.state.q6){
        let s = this.state.arr3[0] + this.state.arr3[1]
        if(s>=2){
            this.setState({part3b:true,part3e:false})
        }else{
            this.setState({part3b:false,part3e:true})
        }
        }
        }

    render(){
        return(
            <div>
                <h4>Over the last 2 weeks, how often have been bothered by the following problems?</h4>
                <div>
                    <p>Little interest or pleasure in doing things</p>
                    <form style={{}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption1 === "0"} 
                                            onChange={this.handleOptionChange1} />
                                As much as I always could
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption1 === "1"} 
                                onChange={this.handleOptionChange1} />
                            Not quite so much now
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption1 === "2"} 
                                onChange={this.handleOptionChange1} />
                                Definitely not so much now
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption1 === "3"} 
                                onChange={this.handleOptionChange1} />
                                Not at all
                            </label>
                        </div>
                            
                    </form>
                    <br />

                    <p>Feeling down, depressed or hopeless</p>
                    <form style={{}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption2 === "0"} 
                                            onChange={this.handleOptionChange2} />
                                Not at all
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption2 === "1"} 
                                onChange={this.handleOptionChange2} />
                            Several Days
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption2 === "2"} 
                                onChange={this.handleOptionChange2} />
                                More than half of the days
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption2 === "3"} 
                                onChange={this.handleOptionChange2} />
                                Nearly everyday
                            </label>
                        </div>
                            
                    </form>
                    <br />
               
                {this.state.arr1[0]} {this.state.arr1[1]}
                </div>
                <a className="myButton" onClick={this.handlepart1}>Confirm</a>
                {this.state.ans1 && this.state.part1b?<div>Book Appointment</div>:null}
                {this.state.ans1 && this.state.part1e?<div>Patient Education</div>:null}

                <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />

                <br />
                <br />




                <div>
                    <p>Feeling nervous. anxious or on edge</p>
                    <form style={{}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption3 === "0"} 
                                            onChange={this.handleOptionChange3} />
                                Not at all
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption3 === "1"} 
                                onChange={this.handleOptionChange3} />
                            Several Days
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption3 === "2"} 
                                onChange={this.handleOptionChange3} />
                                More than half of the days
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption3 === "3"} 
                                onChange={this.handleOptionChange3} />
                                Nearly everyday
                            </label>
                        </div>
                            
                    </form>
                    <br />

                    <p>Not being able to stop or control worrying</p>
                    <form style={{}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption4 === "0"} 
                                            onChange={this.handleOptionChange4} />
                                Not at all
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption4 === "1"} 
                                onChange={this.handleOptionChange4} />
                            Several Days
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption4 === "2"} 
                                onChange={this.handleOptionChange4} />
                                More than half of the days
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption4 === "3"} 
                                onChange={this.handleOptionChange4} />
                                Nearly everyday
                            </label>
                        </div>
                            
                    </form>
                    <br />
               
                {this.state.arr2[0]} {this.state.arr2[1]}
                </div>
                <a className="myButton" onClick={this.handlepart2}>Confirm</a>
                {this.state.ans2 && this.state.part2b?<div>Book Appointment</div>:null}
                {this.state.ans2 && this.state.part2e?<div>Patient Education</div>:null}

                <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />

                <br />
                <br />




                <div>
                    <p>Feeling ashamed of your body</p>
                    <form style={{}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption5 === "0"} 
                                            onChange={this.handleOptionChange5} />
                                Not at all
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption5 === "1"} 
                                onChange={this.handleOptionChange5} />
                            Several Days
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption5 === "2"} 
                                onChange={this.handleOptionChange5} />
                                More than half of the days
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption5 === "3"} 
                                onChange={this.handleOptionChange5} />
                                Nearly everyday
                            </label>
                        </div>
                            
                    </form>
                    <br />

                    <p>Experiencing low self-esteem</p>
                    <form style={{}}>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="0" 
                                            checked={this.state.selectedOption6 === "0"} 
                                            onChange={this.handleOptionChange6} />
                                Not at all
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="1" 
                                checked={this.state.selectedOption6 === "1"} 
                                onChange={this.handleOptionChange6} />
                            Several Days
                            </label>
                        </div>
                        <div className="radio">
                            <label>
                                <input type="radio" value="2" 
                                checked={this.state.selectedOption6 === "2"} 
                                onChange={this.handleOptionChange6} />
                                More than half of the days
                            </label>
                        </div>
                        <div className="radio" >
                            <label>
                                <input type="radio" value="3" 
                                checked={this.state.selectedOption6 === "3"} 
                                onChange={this.handleOptionChange6} />
                                Nearly everyday
                            </label>
                        </div>
                            
                    </form>
                    <br />
               
                {this.state.arr3[0]} {this.state.arr3[1]}
                </div>
                <a className="myButton" onClick={this.handlepart3}>Confirm</a>
                {this.state.ans3 && this.state.part3b?<div>Book Appointment</div>:null}
                {this.state.ans3 && this.state.part3e?<div>Patient Education</div>:null}

                <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />

                <br />
                <br />
                <br />
                <br />
            </div>
        )
    }

}

export default Box1;